@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
body {
  margin: 0;
  font-family: "Varela Round", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.Container {
  display: flex;
  flex-direction: column;
  width: 370px;
  margin: auto;
}

.Navbar {
  display: flex;
  position: fixed;
  width: inherit;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  font-size: 16px;
  font-weight: bold;
  color: #41444b;
  box-shadow: 0 8px 10px 0 rgba(10, 31, 68, 0.1);
  border-radius: 0 0 8px 8px;
  background-color: #fff;
  z-index: 100;
}

.Content {
  overflow-y: auto;
  margin-top: 50px;
  padding: 16px;
}

.InputBox {
  position: relative;
  margin-bottom: 16px;
  z-index: 0;
}

.InputSearch {
  width: 100%;
  height: 35px;
  padding: 8px 8px 8px 30px;
  font-size: 14px;
  color: #41444b;
  font-family: "Varela Round", sans-serif;
  background-color: #e3e4e6;
  border-radius: 8px;
  border: none;
}

.InputSearch:focus {
  outline: none;
}

.ContactList {
  width: 100%;
  display: flex;
  border-radius: 8px;
  box-shadow: 3px 8px 10px 0px rgba(10, 31, 68, 0.1);
  overflow: hidden;
  margin-bottom: 16px;
  background-color: #fff;
  border: solid 1px #e3e4e6;
}

.ContactCaption {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 8px 12px;
}

.ContactName {
  color: #41444b;
  font-weight: bolder;
  font-size: 16px;
  margin-bottom: 8px;
}

.ContactAge {
  color: #727477;
  font-weight: bolder;
  font-size: 10px;
}

.ContactEdit {
  background-color: #e3e4e6;
  border-radius: 0 0 0 8px;
  height: 60px;
  width: 46px;
}

.FormEl {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: inherit;
  position: fixed;
  z-index: 10;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}

.FormTitle {
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 48%;
  left: 20px;
  border-radius: 12px;
  color: #fff;
  background-color: #41444b;
  font-size: 16px;
  padding: 8px 10px 8px 8px;
}

.FormAdd {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px 20px;
  background-color: #fff;
  height: 50%;
  border-radius: 8px 8px 0 0;
  color: #41444b;
}

.InputGroup {
  width: 100%;
}

.Input {
  width: 100%;
  height: 35px;
  padding: 8px 10px;
  font-size: 14px;
  color: #41444b;
  font-family: "Varela Round", sans-serif;
  background-color: #e3e4e6;
  border-radius: 8px;
  border: none;
  margin-bottom: 16px;
}

.Input:focus {
  outline: none;
}

.ButtonGroup {
  display: flex;
  justify-content: space-between;
}

.ButtonCancel {
  color: #fff;
  background-color: #cf7500;
  padding: 8px;
  border-radius: 8px;
  width: 30%;
  text-align: center;
  cursor: pointer;
}

.ButtonSubmit {
  color: #fff;
  background-color: #41444b;
  padding: 8px;
  border-radius: 8px;
  width: 30%;
  text-align: center;
  cursor: pointer;
}

